<template>
  <div id="scroll">
    <!-- 日租房、长短租 -->
    <mt-navbar v-model="selected">
      <mt-tab-item
        id="1"
        @click.native="btnSearch(1)"
      >
        <span>日租</span>
      </mt-tab-item>
      <mt-tab-item
        id="2"
        @click.native="btnSearch(2)"
      >
        <span>长短租</span>
      </mt-tab-item>
    </mt-navbar>
    <div class="status_box">
      <span>结算日</span>
      <span>金额</span>
    </div>
    <!-- 月份 -->
    <div :key="contentKey">
      <div
        v-for="(item,index) in listArr"
        :key="index"
      >
        <div
          v-for="(item,i) in item.child"
          :key="i"
        >
          <div
            @click="toggle(item)"
            class="all"
          >
            <div style="text-align:left;width:70%;">
              <div
                style="font-weight:bold;display: flex;align-items: center;"
                @click.stop="goNext(item.y+'-'+item.m)"
              >{{ item.y }}年{{ item.m }}月
                <van-icon name="arrow" />
              </div>
              <div
                class="money"
                v-if="priceAll[item.y+'-'+item.m+'-come'] || priceAll[item.y+'-'+item.m+'-out']"
              >
                <div style="display:inline-block;text-align:left;">
                  <div style="color:#000;font-weight:bold;">结算金额：<span style="color:#ff7d41;"> ¥{{ Number(priceAll[item.y+'-'+item.m+'-come'] - priceAll[item.y+'-'+item.m+'-out']).toFixed(2) }}</span></div>
                  <div style="color:#888;display:flex;font-size: 12px;">
                    <div>总收入：¥{{ Number(priceAll[item.y+'-'+item.m+'-come']).toFixed(2) }}</div>&ensp;
                    <div>总支出：¥{{ Number(priceAll[item.y+'-'+item.m+'-out']).toFixed(2) }}</div>
                  </div>
                </div>
              </div>
            </div>
            <van-icon :name="item.statu==false?'arrow':'arrow-down'" />
          </div>
          <div>
            <ul
              v-show="item.statu"
              :key="key"
              style="margin-bottom:10px;"
            >
              <li
                v-for="(item_o,index) in options[item.y+'-'+item.m]"
                :key="index"
                class="other_box"
                @click="goNext(item_o)"
              >
                <span class="li-box">
                  <span>
                    {{ item_o.time || item_o.billTime }}</span>
                  <span class="c_00">{{ Number(item_o.price).toFixed(2) || Number(item_o.balanceAmount).toFixed(2) }}
                    <van-icon name="arrow" />
                  </span>
                </span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { GetbusinessLandlordBillRecordBalance, GetbusinessLandlordBillRecordMonthBalance } from '@/api/api_landlord';
import dayjs from 'dayjs';
export default {
  components: {},
  data() {
    return {
      selected: '1',
      key: 0,
      listArr: [],
      item: '',
      statu: false,
      options: {},
      priceAll: {},
      contentKey: 0,
      num: '1'
    };
  },
  created() {
    let star = '2021-09';
    let end = dayjs().format('YYYY-MM');
    let num = 100;
    let timeArr = [];
    for (let i = 0; i < num; i++) {
      let timeUnix = dayjs(star).add(i, 'month').unix();
      if (timeUnix <= dayjs(end).unix()) {
        timeArr.push(dayjs(star).add(i, 'month'));
      }
    }
    this.$store.commit('setHeader', '经营日报');
    let current_time = new Date();
    let year = current_time.getFullYear();
    let mouth = current_time.getMonth() + 1 < 10 ? '0' + (current_time.getMonth() + 1) : current_time.getMonth() + 1;
    for (let i = 0; i < timeArr.length; i++) {
      let tempMouth = mouth - i;
      let tempYear = year;
      if (tempMouth <= 0) {
        let abstempMouth = Math.abs(tempMouth);
        tempMouth = 12 - (abstempMouth % 12);
        let tempYJ = parseInt(abstempMouth / 12) + 1;
        tempYear = tempYear - tempYJ;
      }
      this.listArr.push({
        y: tempYear,
        m: tempMouth < 10 ? '0' + tempMouth : tempMouth,
        statu: i == 0 ? true : false
      });
    }
    let data = {};
    this.listArr.forEach((item) => {
      if (data[item.y]) {
        data[item.y].push(item);
      } else {
        data[item.y] = [item];
      }
    });
    let arr = [];
    for (const k in data) {
      let obj = {};
      obj.year = k;
      obj.child = data[k];
      arr.push(obj);
    }
    this.listArr = arr.reverse();
    this.toggle(this.listArr[0].child[0], 1);
  },
  methods: {
    // 切换
    btnSearch(num) {
      this.listArr.map(item => {
        item.child.map(_itm => { _itm.statu = false })
      })
      let year = new Date().getFullYear();
      this.num = num;
      this.priceAll = {};
      this.options = [];
      this.toggle(this.listArr[0].child[0], 1);
    },
    // 切换滑块
    toggle(item, first) {
      this.item = item;
      if (first == 1) {
        item.statu = true;
      } else {
        item.statu = !item.statu;
      }
      if (this.num == 2) {
        if (item.statu == true) {
          this.getprice_long(item.y, item.m);
        }
      } else {
        if (item.statu == true) {
          this.getprice(item.y, item.m);
        }
      }
    },
    // 日租
    getprice(y, m) {
      let data = {
        billTime: this.item.y + '-' + this.item.m
      };
      GetbusinessLandlordBillRecordBalance(data).done((res) => {
        this.options[y + '-' + m] = [];
        this.order = res.order
        this.other = res.other
        let arr = []
        for (const k in res.data) {
          let obj = {}
          obj.time = k
          obj.price = res.data[k]
          arr.push(obj)
        }
        this.options[y + '-' + m] = arr
        this.func(this.options[y + '-' + m], y + '-' + m);
        ++this.contentKey;
      })
    },
    // 长租
    getprice_long(y, m) {
      let data = {
        billTime: this.item.y + '-' + this.item.m
      };
      GetbusinessLandlordBillRecordMonthBalance(data).done((res) => {
        this.options[y + '-' + m] = [];
        this.order = res.order
        this.other = res.other
        let arr = []
        for (const k in res.data) {
          let obj = {}
          obj.time = k
          obj.price = res.data[k]
          arr.push(obj)
        }
        this.options[y + '-' + m] = arr
        this.func(this.options[y + '-' + m], y + '-' + m);
        ++this.contentKey;
      })
    },
    func(options, ym) {
      options.sort(function (a, b) {
        return a.time > b.time ? -1 : 1
      })
      options.map(item => {
        this.priceAll[ym + '-come'] = Number(this.order).toFixed(2)
        this.priceAll[ym + '-out'] = Number(this.other).toFixed(2)
        ++this.key;
      })
    },
    // 已出账---长租和日租---日账单
    goNext(item) {
      this.$router.push({
        path: '/owner-finance-rooms',
        query: {
          billTime: item.time || item,
          selected: this.selected
        }
      });
    }
  }
};
</script>

<style scoped>
#scroll {
    padding: 15px 0;
    position: fixed;
    top: 50px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
    background-color: #f6f6f6;
    font-size: 14px;
    color: #000;
}
.status_box {
    margin: 10px 0;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    padding: 15px 30px;
    font-weight: bold;
}
ul {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
.c_00 {
    color: rgb(0, 0, 0);
    display: flex;
    align-items: center;
}
.all {
    background: #fff;
    display: flex;
    justify-content: space-between;
    padding: 8px 25px 8px 20px;
    align-items: center;
    line-height: 20px;
}
.money {
    font-size: 12px;
    display: flex;
}
.li-box {
    font-size: 15px;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    padding: 0 15px 0 20px;
}
.other_box {
    color: #888888;
    line-height: 30px;
    border-bottom: 1px solid #f6f6f6;
}
select {
    border: solid 1px #000;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    border: none;
    outline: none;
}
select::-ms-expand {
    display: none;
}
.mint-navbar .mint-tab-item.is-selected {
    color: #ff7d41;
    margin-bottom: 0;
    border-bottom: 3px solid #ff7d41;
}
/* 年账单 */
.yearBill {
    line-height: 30px;
    text-align: left;
    padding-left: 30px;
    display: flex;
    align-items: center;
    color: #ff7d41;
}
/deep/ .van-tab {
    color: #989898;
}
/deep/ .van-tabs__line {
    width: 35%;
    background-color: #ff7d41;
}
/deep/ .van-tab--active {
    color: #ff7d41;
}
</style>



